import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
  loading: false,
  error: "",
  products: [],
  filteredProducts:[]
};
// Add new Product
export const addNewProduct = createAsyncThunk("product/new",async (data)=>{
  const config = {
    headers: {
       "Content-Type": "multipart/form-data",
        Authorization: `Bearer ${localStorage.getItem('auth_token')}`
        } 
   }
  try{
    const res = await axios.post("/api/v1/product/new",data,config)
    console.log(res.data)
    return res.data;
  }
  catch(err){
    throw err.response.data
  }
})
export const getAllProducts = createAsyncThunk("product/all", async () => {
  try {
    const res = await axios.get("/api/v1/product/getproducts");
    return res.data.products;
  } catch (err) {
    console.log(err)
    throw err.response.data
  }
});

//Delete the product 
export const deleteProduct = createAsyncThunk("product/delete",async({productId})=>{
  const config = {
    headers: {
       "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem('auth_token')}`
        } 
   }
  try{
    const res = await axios.delete(`/api/v1/product/delete/${productId}`,config);
    return res.data;
  }
  catch(err){
    throw err.response.data.msg
  }

})


// Search product by name
export const filterProduct = createAsyncThunk("product/search",async(productName)=>{
  try{
    const res = await axios.get(`/api/v1/product/search?name=${productName}`);
    console.log(res.data)
    return res.data;


  }
  catch(err){
    throw err.response.data.msg
  }

})

const productSlice = createSlice({
  name: "products",
  initialState,
  reducers:{
    clearError : (state)=>{
        state.error = ""
    },
},
  extraReducers: (builder) => {
    //Get all products
    builder.addCase(getAllProducts.pending, (state) => {
      state.loading = true;
    });
    builder.addCase(getAllProducts.fulfilled, (state, action) => {
      state.loading = false;
      state.products = action.payload;
    });
    builder.addCase(getAllProducts.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message
    });

    // Get Filtered Product
    builder.addCase(filterProduct.pending, (state) => {
      state.loading = true;
    });
    builder.addCase(filterProduct.fulfilled, (state, action) => {
      state.loading = false;
      state.filteredProducts = action.payload.products;
    });
    builder.addCase(filterProduct.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message
    });
    // Add new Product
    builder.addCase(addNewProduct.pending, (state) => {
      state.loading = true;
    });
    builder.addCase(addNewProduct.fulfilled, (state, action) => {
      state.loading = false;
      state.products = [...state.products,action.payload.product]
    });
    builder.addCase(addNewProduct.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message
    });
    // Delete the product
    builder.addCase(deleteProduct.pending, (state) => {
      state.loading = true;
    });
    builder.addCase(deleteProduct.fulfilled, (state, action) => {
      state.loading = false;
      state.products = action.payload.products
    });
    builder.addCase(deleteProduct.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message
    });

  },
});

export const {clearError} = productSlice.actions ;

export default productSlice.reducer;
