import { Box, Paper, Typography } from "@mui/material";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useSearchParams } from "react-router-dom";
import CartItem from "../../components/Cart Item/CartItem";
import Loader from "../../components/Loader/Loader";
import { getCartItems } from "../../redux/features/cartSlice";
import Wrapper from "../../utils/Wrapper";

const Cart = () => {
  const dispatch = useDispatch();
  const {cartItems,loading,error}  = useSelector(state=>state.cartItems);
  useEffect(() => {
    dispatch(getCartItems())
  }, [dispatch])
  if(error) console.log(error);
  return (
    <>
   {loading ?  <Loader/> : null}
    <Paper
    elevation={1}
    sx={{
      p: 3,
      overflowY:"scroll",
      minHeight: "300px",
      position: "relative",
    }}>
        {
         cartItems.length === 0 ? <Typography variant='h6'>No Items in Cart</Typography> :
            cartItems.map((cartItem,index)=>{
                return <CartItem key={cartItem._id+index} product={cartItem} type="cart"/>
            })
        }
        </Paper>


        </>
  );
};

export default Cart;
