import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"
import axios from "axios";
import {FetchFromApi} from '../../utils/FetchFromAPI';

const initialState = {
    loading:false,
    cartItems:[],
    error:"",
    message:""
}

// const token = localStorage.getItem("auth_token");
// console.log(token)
// const config = {
//     "Content-Type":"application/json",
//     headers:{
//         "Authorization":`Bearer ${token}` 
//     }
// }
const config = { headers: { Authorization: `Bearer ${localStorage.getItem('auth_token')}` } };




export const getCartItems = createAsyncThunk("cart/item",async()=>{
    try{
        const res = await  FetchFromApi("cart/getCartItems");
        return res;
    }
    catch(err){
        throw err.response.data.msg
    }
});


export const deleteCartItem = createAsyncThunk('cart/delete',async({productId})=>{
    try{
        const res = await axios.delete(`/api/v1/cart/delete/${productId}`,config);
        return res.data;
    }
    catch(err){
        throw err.response.data.msg;
    }
})
export const addCartItem = createAsyncThunk('cart/new',async({productId})=>{
    try{
        const res =  await axios.get(`/api/v1/cart/add/${productId}`,config);
        return res.data;
    }
    catch(err){
        throw err.response.data.msg;
    }
})






const cartSlice = createSlice({
    name:"cart",
    initialState,

    reducers:{
        clearError : (state)=>{
            state.error = ""
        },
    },
    extraReducers:(builder)=>{
        builder.addCase(getCartItems.pending,(state)=>{
            state.loading = true;
        });
        builder.addCase(getCartItems.fulfilled,(state,action)=>{
            state.loading = false;
            state.cartItems = action.payload.cart
        })
        builder.addCase(getCartItems.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message;

        });
        builder.addCase(deleteCartItem.pending,(state)=>{
            state.loading =true;
        });
        builder.addCase(deleteCartItem.fulfilled,(state,action)=>{
            state.loading = false;
            state.cartItems = action.payload.cart;
        })

        builder.addCase(deleteCartItem.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message;
        })
        builder.addCase(addCartItem.pending,(state)=>{
            state.loading =true;
        });
        builder.addCase(addCartItem.fulfilled,(state,action)=>{
            state.loading = false;
            state.cartItems = action.payload.cart;
        })

        builder.addCase(addCartItem.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message;
        })
    }
})


export const {clearError} = cartSlice.actions ;

export default cartSlice.reducer