import { Box } from "@mui/system";
import React from "react";

const Wrapper = (props) => {
  return (
    <Box sx={{ backgroundColor: props.bgColor  }}>
      <Box maxWidth={props.width} mx={"auto"}>
        {props.children}
      </Box>
    </Box>
  );
};


Wrapper.defaultProps = {
  bgColor  : "#e0e0e0",
  width : "1300px"
}

export default Wrapper;
