import { Box, keyframes } from "@mui/system";
import React from "react";
const Loader = () => {
  const loader = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
  `;

  return (
    <Box
      sx={{
        height: "100vh",
        position: "absolute",
        left: "0px",
        right: "0px",
        bottom: "0px",
        top: "0px",
        display: "grid",
        placeItems: "center",
      }}
    >
      <Box
        sx={{
          height: "60px",
          width: "60px",
          borderRadius: "50%",
          animation: `infinite 1s linear ${loader}`,
          borderBottom: "3px solid #000",
        }}
      >
      </Box>
    </Box>
  );
};

export default Loader;
