import React, { useState, useEffect, createElement, useMemo } from "react";
import PropTypes from "prop-types";
import SideBar from "../../components/SideBar/SideBar";
import Typography from "@mui/material/Typography";
import { Box, Stack } from "@mui/system";
import Wrapper from "../../utils/Wrapper";
import MyProfile from "./MyProfile/MyProfile";
import MyOrders from "./MyOrders/MyOrders";
import ChangePassword from "./Change Password/ChangePassword";
import AddProduct from "./Add Product/add_product";
import Products from "./Products/products";
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import InventoryIcon from '@mui/icons-material/Inventory';
import KeyIcon from '@mui/icons-material/Key';
import AddCardIcon from '@mui/icons-material/AddCard';
import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import Cart from "../Cart/Cart";

const categories = [
  {
    name: "My Profile",
    component: MyProfile,
    icon:AccountBoxIcon
  },
  {
    name:"Cart",
    component:Cart,
    icon:ShoppingCartIcon
  },
  {
    name: "My Orders",
    component: MyOrders,
    icon:ShoppingCartCheckoutIcon
  },
  {
    name: "Change Password",
    component: ChangePassword,
    icon:KeyIcon
  },
  {
    name: "Add Product",
    component: AddProduct,
    icon:AddCardIcon
  },
  {
    name: "Products",
    component: Products,
    icon:InventoryIcon
  },

];

const Profile = () => {
  const [selectedCategory,setselectedCategory] = useState(categories[0]?.name);
  useEffect(() => {
    document.title = `Vendi Prime | ${selectedCategory}`;
  }, [selectedCategory]);


  const selectedComponent = useMemo(()=> categories.find(
    (category) => category.name === selectedCategory
  )?.component,[selectedCategory]);

  return (
    <Wrapper bgColor="#eff0f4">
      <Stack flexDirection="row" gap={2} p={1} sx={{ minHeight: "600px" }}>
        <SideBar
          selectedCategory={selectedCategory}
          setselectedCategory={setselectedCategory}
          categories={categories}
        />
        <Box flexGrow={1} p={1}> 
          <Typography variant="h5" color="initial" my={2}>
            {selectedCategory}
          </Typography>
          {selectedComponent ? (
            < >
              {createElement(selectedComponent, {
                setselectedCategory:setselectedCategory,
                selectedCategory:selectedCategory,
              })}
            </>
          ) : (
            <Box>
              <Typography variant="body1">
                No component found for selected category.
              </Typography>
            </Box>
          )}
        </Box>
      </Stack>
    </Wrapper>
  );
};

Profile.propTypes = {
  selectedCategory: PropTypes.string,
};

export default Profile;
