import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { FetchFromApi } from "../../utils/FetchFromAPI";


const initialState ={
    loading : false,
    user:{},
    error:"",
    isAuthenticated:false,
    message:""
}





// Register 
export const registerUser = createAsyncThunk("auth/register",async(formData)=>{
    const config = {
        headers: {
           "Content-Type": "multipart/form-data",
            } 
       }
    try{
        const res = await axios.post("/api/v1/user/register",formData,config)
        localStorage.setItem("auth_token",res.data.token)
        return res.data;
    }
    catch(err){
        throw err.response.data.msg
    }
})
//Login
export const login = createAsyncThunk("auth/login",async({email,password})=>{
    try{
        const res =  await axios.post("/api/v1/user/login",{email,password});
        localStorage.setItem("auth_token",res.data.token)
        return res.data;
    }
    catch(err){
        throw err.response
    }

});

//Authenticate
export const authenticate = createAsyncThunk("auth/me",async()=>{
    try{
        const res = await FetchFromApi("user/authenticate")
        return res
    }
    catch(err){
        console.log(err)
        throw err.response.data
    }
})

// Update Profile 
export const updateProfile = createAsyncThunk("profile/update",async(data)=>{
    const config = {
        headers: {
           "Content-Type": "multipart/form-data",
            Authorization: `Bearer ${localStorage.getItem('auth_token')}`
            } 
       }
    try{
        const res  = await axios.put("/api/v1/user/profile/update",data,config);
        console.log(res.data)
        return res.data;
    }
    catch(err){
        console.log(err)
        throw err.response.data
    }
})

// Update Password
export const updatePassword = createAsyncThunk("password/update",async(data)=>{
    const config = {
        headers: {
           "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem('auth_token')}`
            } 
       }

    if(!data) throw new Error("All fields are required")
    try{
        const res = await axios.put('/api/v1/user/changepassword',data,config);
        return res.data
    }
    catch(err){
        throw err.response.data.msg
    }

})


// Get Orders
export const getOders = createAsyncThunk("orders/get",async()=>{
    const config = {
        headers: {
           "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem('auth_token')}`
            } 
       }
    try{
        const res = await axios.get('/api/v1/orders/getOrders',config);
        return res.data
    }
    catch(err){
        throw err.response.data.msg
    }

})

// Create Order
export const createOrder = createAsyncThunk("orders/new",async(orderId)=>{
    const config = {
        headers: {
           "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem('auth_token')}`
            } 
       }
    try{
        const res = await axios.post(`/api/v1/orders/new/${orderId}`,{},config);
        return res.data
    }
    catch(err){
        throw err.response.data.msg
    }
})
export const deleteOrder = createAsyncThunk("orders/delete",async(orderId)=>{
    const config = {
        headers: {
           "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem('auth_token')}`
            } 
       }
    try{
        const res = await axios.delete(`/api/v1/orders/delete/${orderId}`,config);
        return res.data
    }
    catch(err){
        throw err.response.data.msg
    }
})





const userSlice = createSlice({
    name:"auth",
    initialState,
    reducers:{
        clearErrors:(state)=>{
            state.error = ""
        }  
    },
    extraReducers:(builder)=>{
        //Register
        builder.addCase(registerUser.pending,(state)=>{
            state.loading = true;
        })
        builder.addCase(registerUser.fulfilled,(state,action)=>{
            state.loading = false;
            state.user =  action.payload.user;
            state.isAuthenticated = true;
        })
        builder.addCase(registerUser.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message
        })
        //Login
        builder.addCase(login.pending,(state)=>{
            state.loading = true;
        })
        builder.addCase(login.fulfilled,(state,action)=>{
            state.loading = false;
            state.user =  action.payload.user;
            state.isAuthenticated = true;
        })
        builder.addCase(login.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message
        })

        //Authenticate
        builder.addCase(authenticate.pending,(state)=>{
            state.loading = true;
        })
        builder.addCase(authenticate.fulfilled,(state,action)=>{
            state.loading = false;
            state.user =  action.payload.user;
            state.isAuthenticated = true;
        })
        builder.addCase(authenticate.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message
        });
        
        //Update Profile
        builder.addCase(updateProfile.pending,(state)=>{
            state.loading = true;
        });
        builder.addCase(updateProfile.fulfilled,(state,action)=>{
            state.loading = false;
            state.user = action.payload.user;
        })
        builder.addCase(updateProfile.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message;
        })

        // Upadate Password
        builder.addCase(updatePassword.pending,(state)=>{
            state.loading = true;
        });
        builder.addCase(updatePassword.fulfilled,(state,action)=>{
            state.loading = false;
            state.message = action.payload.msg
        })
        builder.addCase(updatePassword.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message;
        })

        // Get Orders
        builder.addCase(getOders.pending,(state)=>{
            state.loading = true;
        });
        builder.addCase(getOders.fulfilled,(state,action)=>{
            state.loading = false;
            state.user.orders = action.payload.orders
            state.message = action.payload.msg
        })
        builder.addCase(getOders.rejected,(state,action)=>{
            state.loading = false;
            state.error = action.error.message;
        })
                // Create Order
                builder.addCase(createOrder.pending,(state)=>{
                    state.loading = true;
                });
                builder.addCase(createOrder.fulfilled,(state,action)=>{
                    state.loading = false;
                    state.user.orders = action.payload.orders
                    state.message = action.payload.msg
                })
                builder.addCase(createOrder.rejected,(state,action)=>{
                    state.loading = false;
                    state.error = action.error.message;
                })
                // Delete deleteOrder
                builder.addCase(deleteOrder.pending,(state)=>{
                    state.loading = true;
                });
                builder.addCase(deleteOrder.fulfilled,(state,action)=>{
                    state.loading = false;
                    state.user.orders = action.payload.orders
                    state.message = action.payload.msg
                })
                builder.addCase(deleteOrder.rejected,(state,action)=>{
                    state.loading = false;
                    state.error = action.error.message;
                })

    }
})

export const {clearErrors} = userSlice.actions;
export default userSlice.reducer