import React, { useState } from "react";
import Typography from "@mui/material/Typography";
import { Avatar, Box, Button, Input, Paper } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import Loader from "../../../components/Loader/Loader";
import { Stack } from "@mui/system";
import { updateProfile } from "../../../redux/features/userSlice";

const MyProfile = ({selectedCategory,setselectedCategory}) => {
  const dispatch = useDispatch()
  const [editProfile, seteditProfile] = useState(false);
  const [previewImage, setpreviewImage] = useState()
  const { loading, user } = useSelector((state) => state.user);
  const [data, setdata] = useState(user);
  const [image, setimage] = useState()


  const saveProfile = async(e)=>{
    const formData =  new FormData(e.currentTarget);
    console.log(formData)
    dispatch(updateProfile(formData))
    seteditProfile(false);
  }
  const handleImageChange = (e) => {
    setpreviewImage(URL.createObjectURL(e.target.files[0]));
    setimage(e.target.files[0])
  };
  if (loading) return <Loader />;
  return (
    <Paper elevation={1}
    component="form"
    onSubmit={saveProfile}
    sx={{ p: 3,
     }}>
      <Stack
        flexDirection={{ sm: "column", md: "row" }}
        justifyContent="space-between"
      >
        <Box>
          <Typography variant="subtitle2" color="#999">
            Full name
          </Typography>
          {
            editProfile ? <Input value={data.name} name='name' color="secondary" onChange={(e)=>setdata({...data,name:e.currentTarget.value})} /> : 
            <Typography variant="subtitle2" color="#000" my={1.5}>{user.name}</Typography> 
          }
        </Box>
        <Box>
          <Typography variant="subtitle2" color="#999" >
            Email Address
          </Typography>
            <Typography variant="subtitle2" color="#000" my={1.5}>{user.email}</Typography> 
        </Box>
        <Box>
          <Typography variant="subtitle2" color="#999">
            Mobile
          </Typography>
          {
            editProfile ? <Input value={data.mobile}  name='mobile' color="secondary" onChange={(e)=>setdata({...data,mobile:e.currentTarget.value})}/> : 
          <Typography
          variant="subtitle2"
          color={user?.mobile ? "#000" : "#999"}
          my={1.5}
          >
            {user.mobile ? user.mobile : "Please enter your mobile "}
          </Typography>
          }
        </Box>
      </Stack>

      <Avatar 
        src= { previewImage ? previewImage : user.profile.url}
        sx={{ height: "200px", width: "200px",my:1}} 
        /> 
      {
        editProfile ? 
        <Button variant="contained" color="secondary" component="label" onChange={handleImageChange}>
                  Choose Image
                  <input  accept="image/*" hidden name="profile" multiple type="file" />
                </Button> :
                null
          }
    

       {
        editProfile ? <Button variant="contained" color="primary"  sx={{mx:1,my:1}}  type="submit">Save Changes</Button> : 
        <>
      <Button variant="contained" color="secondary" sx={{ mx: 1 ,my:1}} onClick={()=>seteditProfile(true)}>
        Edit Profile
      </Button>
      <Button variant="contained" color="secondary" sx={{ mx: 1 ,my:1}} onClick={()=>setselectedCategory("Change Password")}>
        Change Password
      </Button>
        </>
      }
    </Paper>
  );
};

export default MyProfile;
