import {  Divider, Typography } from "@mui/material";
import { Stack } from "@mui/system";
import React from "react";


const SideBar = ({ setselectedCategory, categories, selectedCategory }) => {
  return (
    <Stack
      direction={"column"}
      gap={2}
      p={{xs:1,sm:2}}
      width={{md:"auto"}}
      justifyContent={"center"}
      height="fit-content"
      sx={{borderRadius: "10px" }}
    >
      {categories.map((category, index) => {
        const Icon = category.icon;


        return (
          <Stack
          direction="row"
          alignItems={"center"}
           key={index} onClick={() => setselectedCategory(category.name)}>
             <Icon 
             fontSize={"medium"} 
             style={{
              color:selectedCategory === category.name ? "#ff7904" : "#757575",cursor:"pointer",
            }}/>
            <Typography
              variant="body2"
              color={selectedCategory === category.name ? "#ff7904" : "#757575"}
              sx={{ p: 1, cursor: "pointer" ,display:{xs:"none",md:"block"} }}
            >
              {category.name}
            </Typography>
            <Divider />
          </Stack>
        );
      })}
    </Stack>
  );
};

export default SideBar;
